.border-radius-8 {
  border-radius: rem(8px);
}

.radius-50 {
  border-radius: 50%;
  overflow: hidden;
}

/***********************************
// Payment Account Option
**************************************/
.payment-btn {
  width: 120px;
  height: 90px;
  border: 1px dashed transparent;

  @media (max-width: 576px) {
    width: 60px;
    height: 60px;
  }
}

.dashed_border {
  border-color: $primaryColor !important;
}

.dashed_border span {
  color: $primaryColor;
}

/***********************************
// Change Password Modal
**************************************/
.password-change {
  position: relative;
}

.password-change span {
  position: absolute;
  right: 1%;
  top: 50%;
  transform: translateY(-50%);
}

.changePasswordModal-content {
  box-shadow: 1px 2px 10px -1px #000;
}

/***********************************
// Add Payment Account
**************************************/
.user_img label {
  background: $white;
  width: 96px;
  height: 96px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.user_img img {
  width: 56px;
  height: 56px;
}

.setting-user-img label {
  width: 128px;
  height: 128px;
}

.user_btn .rs-btn {
  display: inline-flex !important;
}

.linkColor {
  color: $linkColor;
  transition: 5s ease-in-out;
}

.linkColor:hover {
  text-decoration: underline;
}

.changePasswordModal-content.addUser-close .close {
  top: 5%;
}

/***********************************
// accountName
**************************************/
.ra-custom-bg {
  background-color: #f5f5f5 !important;
}

/***********************************
// Change Password Modal
**************************************/
.changePasswordModal-content .close {
  position: absolute;
  top: 7%;
  right: 3%;
  height: 14px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 7px;
}

/***********************************
// Add Payment Option Modal
**************************************/
.changePasswordModal-content.payment-close .close {
  top: 4%;
  right: 4%;
}

.changePasswordModal-content.editAccount-close .close {
  top: 5%;
  right: 4%;
}

/***********************************
// Acitvity Log
**************************************/
.ra-activity-log-wrapper {
  border: 1px solid $primaryBg;
  position: relative;

  @media (max-width: 767px) {
    text-align: center;
  }
}

@media (max-width: 767px) {
  .acitvity-log-select {
    max-width: 400px;
    width: 100% !important;
  }
}

.acitivity-icon-wrapper {
  background: $tableRowColor;
  max-width: 48px;
  height: 48px;
  border-radius: 50%;
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  @media (max-width: 767px) {
    position: unset;
    margin: auto;
    margin-top: 25px;
  }
}

.activity-icon {
  max-width: 21px;
}

.ra-activity-log-text {
  @media (max-width: 767px) {
    padding: 0px !important;
    margin: 0px !important;
  }
}

/***********************************
// Process Payment Modal
**************************************/
.process-payment-btn {
  width: 80px;
  height: 60px;
  border: 2px solid transparent;
  padding: 0px;
  background-color: $tableRowColor;
}

.border-gray-2px {
  border-color: $primaryColor !important;
}

.processPaymentModal-content .close {
  top: 2%;
  right: 2%;
  position: absolute;
}

/***********************************
// Payment Details Modal
**************************************/
.paymentDetails-close .close {
  top: 3%;
  right: 2%;
}

.border-bottom-1 {
  border-bottom: 1px solid $tableRowColor;
}

.border-bottom-1:last-child {
  border-bottom: 1px solid transparent;
}

/***********************************
// Influencer All
**************************************/
.influencer-top {
  width: 50%;

  @media (max-width: 992px) {
    width: 100%;
  }
}

.influencer-arrow {
  color: #475364;
}

.campaign-influencer {
  width: 50%;

  @media (max-width: 768px) {
    width: 100%;
  }
}

/***********************************
// Influencer Progress Bar
**************************************/
.influencer-step-form{
  .step-menu{
    position: relative;
    padding-bottom: 16px;
    overflow: hidden;

    .progress-bar{
      position: absolute;
      overflow: hidden;
      width: 100%;
      background-color: $grey-light;
      height: 8px;
      border-radius: 20px;
      left: 0;
      bottom: 0;  

      & > span{
        position: absolute;
        top: 0;
        left: 0;
        min-width: 32px;
        height: 100%;
        background-color: $primaryColor;
        border-radius: 20px;
        transition: width .3s ease-in-out;
      }
    }

    ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      li{
        font-size: rem(20);
        color: #8E8E93;
        font-weight: 500;
        &.stepped {
          color: $primaryColor;
        }
      }
    }
  }

  .step-content{
    .step{
      display: none;
      &.show {
        display: block;
      }
    }
  }
}